<template>
  <div id="mobileUserEdit">
    <Modal
      v-model="mobileUserRebuild"
      title="编辑策略">
      <Form ref="edit" :model="EditData" :rules="EditRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="cycle" label="周期：">
          <Input v-model.trim="EditData.cycle" placeholder="请输入周期" size="default"/>
        </FormItem>
        <FormItem prop="type" label="类型：">
          <Input v-model.trim="EditData.type" placeholder="请输入类型" size="default"/>
        </FormItem>
        <FormItem prop="time" label="时间：">
          <DatePicker type="date" v-model.trim="EditData.time" placement="bottom-end" placeholder="请选择时间" style="width: 100%" size="small"></DatePicker>
        </FormItem>
        <FormItem prop="remark" label="描述：">
          <Input v-model.trim="EditData.remark" placeholder="请输入描述" size="default"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="editSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { lsDataStrategyUpdate } from '@/api/systemManage'
export default {
  name: 'strategyEdit',
  data () {
    return {
      EditRule: {
        cycle: [
          { required: true, message: '周期不能为空', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '类型不能为空', trigger: 'blur' }
        ],
        time: [
          { required: true, message: '时间不能为空', trigger: 'blur', type: 'date' }
        ]
      },
      /* 编辑框 */
      mobileUserRebuild: false,
      /* 编辑数据 */
      EditData: {}
    }
  },
  props: {
    /* 行政区划数据 */
    codeData: {},
    /* 编辑行原数据 */
    editDefaultData: {}
  },
  watch: {
    editDefaultData (val) {
      this.EditData = val
    }
  },
  mounted () {
  },
  methods: {
    /* 编辑 */
    editSubmit () {
      this.$refs.edit.validate(valid => {
        if (valid) {
          lsDataStrategyUpdate({
            data: {
              ...this.EditData,
              timeStr: this.EditData.time
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$Notice.success({ title: '编辑策略成功' })
              this.$emit('getTableData')
              this.mobileUserRebuild = false
              this.data = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.EditData = {}
    }
  }
}
</script>

<style scoped lang="scss">
</style>
