<template>
  <div id="mobileUserAdd">
    <Modal
      v-model="mobileUserBuild"
      title="新建策略"
      @on-cancel="reset"
    >
      <Form ref="add" :model="AddData" :rules="AddRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="cycle" label="周期：">
          <Input v-model.trim="AddData.cycle" placeholder="请输入周期" size="default"/>
        </FormItem>
        <FormItem prop="type" label="类型：">
          <Input v-model.trim="AddData.type" placeholder="请输入类型" size="default"/>
        </FormItem>
        <FormItem prop="timeStr" label="时间：">
          <DatePicker type="date" v-model.trim="AddData.timeStr" placement="bottom-end" placeholder="请选择时间" style="width: 100%" size="small"></DatePicker>
        </FormItem>
        <FormItem prop="remark" label="描述：">
          <Input v-model.trim="AddData.remark" placeholder="请输入描述" size="default"/>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { lsDataStrategyAdd } from '@/api/systemManage'
export default {
  name: 'strategyAdd',
  data () {
    return {
      /* 添加数据 */
      AddData: {},
      AddRule: {
        cycle: [
          { required: true, message: '周期不能为空', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '类型不能为空', trigger: 'blur' }
        ],
        timeStr: [
          { required: true, message: '时间不能为空', trigger: 'blur', type: 'date' }
        ]
      },
      /* 新建框 */
      mobileUserBuild: false
    }
  },
  props: {
    /* 行政区划数据 */
    codeData: {}
  },
  methods: {
    /* axios: 添加数据 */
    addSubmit () {
      this.$refs.add.validate(valid => {
        if (valid) {
          lsDataStrategyAdd({
            data: {
              ...this.AddData
            }
          }).then(res => {
            if (res.data.code === '0000') {
              this.$emit('getTableData')
              this.$Notice.success({ title: '新建策略成功' })
              this.mobileUserBuild = false
              this.AddData = {}
              this.reset()
            } else {
              this.$Notice.error({ title: res.data.msg })
            }
          })
        }
      })
    },
    // 重置表單參數
    reset () {
      this.AddData = {}
      this.$refs.add.resetFields()
    }
  }
}
</script>

<style scoped lang="scss">
</style>
